.header {
  display: flex;
  align-items: center;
  height: 100vh;
}

.music-control {
  position: absolute;
  z-index: 10;
  top: 25px;
  left: 15px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mute-button {
  background: none;
  border: none;
  color: #000000;
  cursor: pointer;
  font-size: 16px;
}

.header h1 {
  font-family: "THE AMAZING SPIDER-MAN", sans-serif;
  color: yellow;
  font-size: 70px;
  position: relative;
  top: 200px;
}

.text-container {
  position: absolute;
  margin-left: 140px;
  margin-right: 200px;
  margin-bottom: 380px;
}

.info {
  display: flex;
  align-items: center;
  height: 45vh;
  padding: 60px;
  line-height: 1.5;
  font-size: larger;
}

.image-container {
  flex: 0 0 auto;
  margin-right: 0px;
}

.header img {
  width: 30%;
  position: relative;
  left: 50%;
  top: 30px;
}

.web-container {
  position: absolute;
  z-index: 10;
  left: 15%;
  top: 260px;
  width: 70%;
}

.comic {
  position: relative;
  left: 9%;
  margin-top: 100px;
  height: 100vh;
}

.parallax-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  overflow: hidden;
  background-color: #000000;
  margin-top: 100px;
}

.parallax-image {
  flex: 1;
  text-align: center;
  max-width: 17%;
}


.section-three {
  white-space: nowrap;
  display: flex;
  margin-left: 3.5%;
  overflow-x: hidden;
  height: 100vh;
}

.images-container {
  display: flex;
}

.section-three-item {
  margin-right: 30px;
}

.section-three-img {
  width: 450px;
  height: auto;
  border-radius: 30px;
}

.sectionThreefont {
  font-size: 20px;
  text-align: center;
  background-color: rgba(255, 0, 0, 0.557);
  padding: 5px;
  border-radius: 30px;
  margin-top: 200px;
}

.sectionFive {
  height: 100vh;
}

.sectionFive svg {
  position: relative;
  top: 50px;
  left: 25%;
}

/* Custom cursor */

.custom-cursor {
  width: 35px;
  height: 35px;
  background: red;
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  z-index: 1000;
  mix-blend-mode: difference;
  cursor: none !important;
}
